import React from 'react';
import { Container, Typography, Button, Box } from '@mui/material';

export default function Prices() {
  return (
    <Container maxWidth="sm" sx={{ my: 8 }}>
      <Typography variant="h4" sx={{ fontWeight: 700, mb: 3, color: '#1976d2' }}>
        Прайс-листы
      </Typography>
      <Typography variant="body1" sx={{ mb: 2 }}>
        Вы можете скачать актуальный прайс-лист на всё оборудование и услуги:
      </Typography>
      <Box sx={{ textAlign: 'center', my: 4 }}>
        <Button variant="contained" color="primary" href="/files/price-list.pdf" target="_blank">
          Скачать прайс-лист (PDF)
        </Button>
      </Box>
      <Typography variant="body2" color="text.secondary">
        Если вы не нашли нужную позицию — свяжитесь с нами для индивидуального предложения.
      </Typography>
    </Container>
  );
} 