import React from 'react';
import { Container, Typography, List, ListItem, ListItemText } from '@mui/material';

const services = [
  'Проектирование систем безопасности',
  'Монтаж и установка видеонаблюдения',
  'Обслуживание и ремонт оборудования',
  'Консультации и подбор решений',
  'Интеграция с умным домом',
];

export default function Services() {
  return (
    <Container maxWidth="md" sx={{ my: 8 }}>
      <Typography variant="h4" sx={{ fontWeight: 700, mb: 3, color: '#1976d2' }}>
        Наши услуги
      </Typography>
      <Typography variant="body1" sx={{ mb: 2 }}>
        Мы предлагаем полный спектр услуг по обеспечению безопасности объектов любой сложности:
      </Typography>
      <List>
        {services.map((service, idx) => (
          <ListItem key={idx}>
            <ListItemText primary={service} />
          </ListItem>
        ))}
      </List>
    </Container>
  );
} 