import React from 'react';
import { Container, Typography, List, ListItem, ListItemText } from '@mui/material';

const advantages = [
  'Более 10 лет на рынке систем безопасности',
  'Собственный склад и сервисный центр',
  'Сертифицированные специалисты',
  'Гарантия на все виды работ и оборудование',
  'Индивидуальный подход к каждому клиенту',
];

export default function Company() {
  return (
    <Container maxWidth="md" sx={{ my: 8 }}>
      <Typography variant="h4" sx={{ fontWeight: 700, mb: 3, color: '#1976d2' }}>
        О компании
      </Typography>
      <Typography variant="body1" sx={{ mb: 2 }}>
        NVVIDEO — федеральная сеть по продаже и установке систем безопасности. Мы предлагаем комплексные решения для частных лиц, бизнеса и государственных организаций.
      </Typography>
      <List>
        {advantages.map((adv, idx) => (
          <ListItem key={idx}>
            <ListItemText primary={adv} />
          </ListItem>
        ))}
      </List>
    </Container>
  );
} 