import React from 'react';
import { Container, Typography, List, ListItem, ListItemText } from '@mui/material';

const deliveryOptions = [
  'Курьерская доставка по Москве и области',
  'Самовывоз со склада или павильона',
  'Доставка транспортными компаниями по России',
];

const paymentOptions = [
  'Наличный расчёт',
  'Безналичный расчёт (счёт для юр. лиц)',
  'Оплата картой на сайте или при получении',
];

export default function Delivery() {
  return (
    <Container maxWidth="md" sx={{ my: 8 }}>
      <Typography variant="h4" sx={{ fontWeight: 700, mb: 3, color: '#1976d2' }}>
        Доставка и оплата
      </Typography>
      <Typography variant="h6" sx={{ mt: 3, mb: 1 }}>Способы доставки:</Typography>
      <List>
        {deliveryOptions.map((opt, idx) => (
          <ListItem key={idx}><ListItemText primary={opt} /></ListItem>
        ))}
      </List>
      <Typography variant="h6" sx={{ mt: 3, mb: 1 }}>Способы оплаты:</Typography>
      <List>
        {paymentOptions.map((opt, idx) => (
          <ListItem key={idx}><ListItemText primary={opt} /></ListItem>
        ))}
      </List>
    </Container>
  );
} 