import React from 'react';
import { Container, Typography, Grid, Paper } from '@mui/material';

const saleItems = [
  { name: 'Комплект видеонаблюдения', price: '19 990 ₽', oldPrice: '25 990 ₽', desc: 'Готовое решение для дома или офиса.' },
  { name: 'IP-камера Hikvision', price: '4 990 ₽', oldPrice: '6 490 ₽', desc: 'Скидка на популярную модель месяца.' },
  { name: 'Видеорегистратор Dahua', price: '7 990 ₽', oldPrice: '10 990 ₽', desc: 'Надёжный регистратор по спеццене.' },
];

export default function Sale() {
  return (
    <Container maxWidth="md" sx={{ my: 8 }}>
      <Typography variant="h4" sx={{ fontWeight: 700, mb: 3, color: '#ff4c29' }}>
        Распродажа! Специальные предложения
      </Typography>
      <Grid container spacing={3}>
        {saleItems.map((item, idx) => (
          <Grid item xs={12} sm={6} md={4} key={idx}>
            <Paper elevation={3} sx={{ p: 3, textAlign: 'center', background: '#fff7f4' }}>
              <Typography variant="h6" sx={{ fontWeight: 600 }}>{item.name}</Typography>
              <Typography variant="body2" color="text.secondary" sx={{ mb: 1 }}>{item.desc}</Typography>
              <Typography variant="body1" sx={{ textDecoration: 'line-through', color: '#888' }}>{item.oldPrice}</Typography>
              <Typography variant="h5" sx={{ color: '#ff4c29', fontWeight: 700 }}>{item.price}</Typography>
            </Paper>
          </Grid>
        ))}
      </Grid>
    </Container>
  );
} 