import React from 'react';
import { Box, Container, Grid, Typography, Link, List, ListItem, ListItemButton, ListItemText } from '@mui/material';
import PhoneIcon from '@mui/icons-material/Phone';
import EmailIcon from '@mui/icons-material/Email';

const catalogLinks = [
  { label: 'Видеонаблюдение', to: '/catalog?category=1' },
  { label: 'Системы контроля и управления доступом', to: '/catalog?category=2' },
  { label: 'Домофоны и переговорные устройства', to: '/catalog?category=13' },
  { label: 'Охранно-пожарные системы', to: '/catalog?category=14' },
  { label: 'Оборудование СКС', to: '/catalog?category=15' },
  { label: 'Кабельная продукция', to: '/catalog?category=16' },
  { label: 'Сетевое оборудование', to: '/catalog?category=17' },
  { label: 'Источники питания', to: '/catalog?category=18' },
  { label: 'Системы оповещения и трансляции', to: '/catalog?category=19' },
  { label: 'Монтажные и расходные материалы', to: '/catalog?category=20' },
  { label: 'Инструменты', to: '/catalog?category=21' },
  { label: 'Жесткие диски и карты памяти', to: '/catalog?category=22' },
  { label: 'Умный дом', to: '/catalog?category=23' },
  { label: 'Проектное оборудование', to: '/catalog?category=24' },
  { label: 'Умные камеры', to: '/catalog?category=25' },
];

const infoLinks = [
  { label: 'О компании', to: '/company' },
  { label: 'Новости', to: '/news' },
  { label: 'Где купить', to: '/contacts' },
  { label: 'Доставка и оплата', to: '/delivery' },
  { label: 'Распродажа', to: '/sale' },
  { label: 'Обмен и возврат', to: '/return' },
  { label: 'Вопрос-ответ', to: '/faq' },
  { label: 'Франшиза', to: '/franchise' },
  { label: 'Акции', to: '/sale' },
  { label: 'Публичная оферта', to: '/offer', highlight: true },
  { label: 'Политика конфиденциальности', to: '/privacy' },
];

export default function Footer() {
  return (
    <Box
      component="footer"
      sx={{
        bgcolor: '#393939',
        color: 'white',
        py: { xs: 4, sm: 6 },
        fontSize: { xs: '14px', sm: '15px' },
        width: '100vw',
        marginX: 'calc(50% - 50vw)',
      }}
    >
      <Container maxWidth="lg" sx={{ py: { xs: 2, md: 4 } }}>
        <Grid container spacing={2} direction={{ xs: 'column', md: 'row' }}>
          <Grid item xs={12} md={4}>
            <Typography 
              variant="subtitle1" 
              sx={{ 
                mb: { xs: 1, sm: 2 }, 
                textTransform: 'uppercase', 
                fontSize: { xs: '15px', sm: '16px' }, 
                fontWeight: 600,
                color: 'primary.main'
              }}
            >
              Каталог
            </Typography>
            <List dense sx={{ p: 0 }}>
              {catalogLinks.map((item, idx) => (
                <ListItem key={idx} disablePadding sx={{ py: 0 }}>
                  <ListItemButton 
                    component={Link} 
                    to={item.to} 
                    sx={{ 
                      py: 0, 
                      px: 0, 
                      color: 'inherit', 
                      '&:hover': { color: 'secondary.main' },
                      minHeight: { xs: 32, sm: 36 }
                    }}
                  >
                    <ListItemText 
                      primary={item.label} 
                      sx={{ 
                        '& .MuiTypography-root': { 
                          fontSize: { xs: '13px', sm: '15px' }, 
                          fontWeight: 400,
                          whiteSpace: 'nowrap',
                          overflow: 'hidden',
                          textOverflow: 'ellipsis'
                        } 
                      }} 
                    />
                  </ListItemButton>
                </ListItem>
              ))}
            </List>
          </Grid>
          <Grid item xs={12} md={4}>
            <Typography 
              variant="subtitle1" 
              sx={{ 
                mb: { xs: 1, sm: 2 }, 
                textTransform: 'uppercase', 
                fontSize: { xs: '15px', sm: '16px' }, 
                fontWeight: 600,
                color: 'primary.main'
              }}
            >
              О компании
            </Typography>
            <List dense sx={{ p: 0 }}>
              {infoLinks.map((item, idx) => (
                <ListItem key={idx} disablePadding sx={{ py: 0 }}>
                  <ListItemButton 
                    component={Link} 
                    to={item.to} 
                    sx={{ 
                      py: 0, 
                      px: 0, 
                      color: item.highlight ? 'secondary.main' : 'inherit', 
                      fontWeight: item.highlight ? 700 : 400,
                      textTransform: item.highlight ? 'uppercase' : 'none',
                      transition: 'color 0.2s', 
                      '&:hover': { color: '#fff' }, 
                      minHeight: { xs: 32, sm: 36 }
                    }}
                  >
                    <ListItemText 
                      primary={item.label} 
                      sx={{ 
                        '& .MuiTypography-root': { 
                          fontSize: { xs: '13px', sm: '15px' }, 
                          fontWeight: item.highlight ? 700 : 400, 
                          textTransform: item.highlight ? 'uppercase' : 'none',
                          whiteSpace: 'nowrap',
                          overflow: 'hidden',
                          textOverflow: 'ellipsis'
                        } 
                      }} 
                    />
                  </ListItemButton>
                </ListItem>
              ))}
            </List>
            <Box sx={{ mt: { xs: 2, sm: 3 } }}>
              <Box sx={{ 
                display: 'flex', 
                alignItems: 'center', 
                mb: 1,
                fontSize: { xs: '13px', sm: '15px' }
              }}>
                <PhoneIcon sx={{ fontSize: { xs: 16, sm: 18 }, mr: 1 }} />
                <Typography component="span">+7 926 181-01-42</Typography>
              </Box>
              <Box sx={{ 
                display: 'flex', 
                alignItems: 'center', 
                mb: 1,
                fontSize: { xs: '13px', sm: '15px' }
              }}>
                <EmailIcon sx={{ fontSize: { xs: 16, sm: 18 }, mr: 1 }} />
                <Typography component="span">nvvideo.ru@gmail.com</Typography>
              </Box>
            </Box>
          </Grid>
          <Grid item xs={12} md={4}>
            <Typography 
              variant="subtitle1" 
              sx={{ 
                mb: { xs: 1, sm: 2 }, 
                textTransform: 'uppercase', 
                fontSize: { xs: '15px', sm: '16px' }, 
                fontWeight: 600,
                color: 'primary.main'
              }}
            >
              Контакты
            </Typography>
            <Box sx={{ 
              display: 'flex', 
              flexDirection: 'column',
              gap: { xs: 1, sm: 1.5 }
            }}>
              <Typography sx={{ fontSize: { xs: '13px', sm: '15px' } }}>
                Адрес: Ш.Автомагистраль. Москва-Нижний Новгород. вл19Н, Реутов<br />Строительный рынок, павильон 17b
              </Typography>
              <Typography sx={{ fontSize: { xs: '13px', sm: '15px' } }}>
                Время работы: Ежедневно 9.00-21.00
              </Typography>
              <Typography sx={{ fontSize: { xs: '13px', sm: '15px' } }}>
                ИНН: 772317907300 ИП Новиков А.Е.
              </Typography>
            </Box>
          </Grid>
        </Grid>
        <Box sx={{ textAlign: 'center', mt: 2, fontSize: { xs: '0.9rem', sm: '1rem' } }}>
          © {new Date().getFullYear()} NVVIDEO. Все права защищены.
        </Box>
      </Container>
    </Box>
  );
} 