import React from 'react';
import { Container, Typography, List, ListItem, ListItemText } from '@mui/material';

const returnRules = [
  'Возврат товара возможен в течение 14 дней с момента покупки при сохранении товарного вида и упаковки.',
  'Для возврата необходим чек или иной документ, подтверждающий покупку.',
  'Обмен товара осуществляется при наличии заводского брака или несоответствия заказу.',
  'Денежные средства возвращаются тем же способом, которым была произведена оплата.',
];

export default function Return() {
  return (
    <Container maxWidth="md" sx={{ my: 8 }}>
      <Typography variant="h4" sx={{ fontWeight: 700, mb: 3, color: '#1976d2' }}>
        Обмен и возврат
      </Typography>
      <List>
        {returnRules.map((rule, idx) => (
          <ListItem key={idx}><ListItemText primary={rule} /></ListItem>
        ))}
      </List>
      <Typography variant="body2" color="text.secondary" sx={{ mt: 3 }}>
        Если у вас возникли вопросы по возврату или обмену товара, свяжитесь с нами по телефону или email, указанным в разделе "Контакты".
      </Typography>
    </Container>
  );
} 