import React from 'react';

const PaymentFail = () => (
  <div style={{ textAlign: 'center', marginTop: '100px' }}>
    <h1>Оплата не удалась</h1>
    <p>К сожалению, произошла ошибка при оплате. Попробуйте еще раз или свяжитесь с поддержкой.</p>
    <a href="/" style={{ color: '#1976d2', textDecoration: 'underline' }}>Вернуться на главную</a>
  </div>
);

export default PaymentFail; 