import React from 'react';
import { Container, Typography, Paper, Box } from '@mui/material';

const newsList = [
  {
    title: 'Открытие нового павильона',
    date: '2025-06-01',
    text: 'Мы открыли новый павильон на строительном рынке Реутова. Ждём вас за выгодными предложениями!'
  },
  {
    title: 'Скидки на IP-камеры',
    date: '2025-05-20',
    text: 'В июне действует специальная акция на IP-камеры Hikvision и Dahua.'
  }
];

export default function News() {
  return (
    <Container maxWidth="md" sx={{ my: 8 }}>
      <Typography variant="h4" sx={{ fontWeight: 700, mb: 3, color: '#1976d2' }}>
        Новости
      </Typography>
      {newsList.length === 0 ? (
        <Typography>Пока новостей нет.</Typography>
      ) : (
        newsList.map((news, idx) => (
          <Paper key={idx} sx={{ p: 3, mb: 3 }}>
            <Typography variant="h6" sx={{ fontWeight: 600 }}>{news.title}</Typography>
            <Typography variant="caption" color="text.secondary">{news.date}</Typography>
            <Box sx={{ mt: 1 }}>{news.text}</Box>
          </Paper>
        ))
      )}
    </Container>
  );
} 