import React from 'react';
import { Container, Typography, Accordion, AccordionSummary, AccordionDetails } from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

const faqList = [
  {
    question: 'Как оформить заказ?',
    answer: 'Выберите нужный товар, добавьте его в корзину и оформите заказ через сайт или по телефону.'
  },
  {
    question: 'Какие способы оплаты доступны?',
    answer: 'Вы можете оплатить заказ наличными, банковской картой или по безналичному расчёту.'
  },
  {
    question: 'Как осуществляется доставка?',
    answer: 'Доставка возможна курьером по Москве и области, самовывозом или транспортной компанией по России.'
  },
  {
    question: 'Можно ли вернуть товар?',
    answer: 'Да, возврат возможен в течение 14 дней при сохранении товарного вида и упаковки.'
  }
];

export default function Faq() {
  return (
    <Container maxWidth="md" sx={{ my: 8 }}>
      <Typography variant="h4" sx={{ fontWeight: 700, mb: 3, color: '#1976d2' }}>
        Вопрос-ответ
      </Typography>
      {faqList.map((item, idx) => (
        <Accordion key={idx} sx={{ mb: 2 }}>
          <AccordionSummary expandIcon={<ExpandMoreIcon />}>
            <Typography sx={{ fontWeight: 600 }}>{item.question}</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>{item.answer}</Typography>
          </AccordionDetails>
        </Accordion>
      ))}
    </Container>
  );
} 