import React from 'react';
import { Container, Typography, List, ListItem, ListItemText } from '@mui/material';

const advantages = [
  'Готовая бизнес-модель и поддержка на всех этапах',
  'Обучение персонала и маркетинговая поддержка',
  'Поставки оборудования по специальным ценам',
  'Эксклюзив на регион',
  'Помощь в запуске и развитии бизнеса',
];

export default function Franchise() {
  return (
    <Container maxWidth="md" sx={{ my: 8 }}>
      <Typography variant="h4" sx={{ fontWeight: 700, mb: 3, color: '#1976d2' }}>
        Франшиза NVVIDEO
      </Typography>
      <Typography variant="body1" sx={{ mb: 2 }}>
        Присоединяйтесь к федеральной сети NVVIDEO и откройте собственный бизнес в сфере систем безопасности!
      </Typography>
      <List>
        {advantages.map((adv, idx) => (
          <ListItem key={idx}><ListItemText primary={adv} /></ListItem>
        ))}
      </List>
      <Typography variant="body2" color="text.secondary" sx={{ mt: 3 }}>
        Для получения подробной информации и условий сотрудничества свяжитесь с нами по телефону или email, указанным в разделе "Контакты".
      </Typography>
    </Container>
  );
} 