import React from 'react';
import { Container, Typography, Grid, Paper } from '@mui/material';

const brands = [
  { name: 'Hikvision', description: 'Мировой лидер в области видеонаблюдения.' },
  { name: 'Dahua', description: 'Надёжные системы безопасности и видеонаблюдения.' },
  { name: 'Axis', description: 'Профессиональные IP-камеры и решения.' },
  { name: 'Satvision', description: 'Российский производитель оборудования для видеонаблюдения.' },
  { name: 'Tantos', description: 'Системы контроля доступа и видеонаблюдения.' },
];

export default function Brands() {
  return (
    <Container maxWidth="md" sx={{ my: 8 }}>
      <Typography variant="h4" sx={{ fontWeight: 700, mb: 3, color: '#1976d2' }}>
        Популярные бренды
      </Typography>
      <Grid container spacing={3}>
        {brands.map((brand, idx) => (
          <Grid item xs={12} sm={6} md={4} key={idx}>
            <Paper elevation={2} sx={{ p: 3, textAlign: 'center' }}>
              <Typography variant="h6" sx={{ fontWeight: 600 }}>{brand.name}</Typography>
              <Typography variant="body2" color="text.secondary">{brand.description}</Typography>
            </Paper>
          </Grid>
        ))}
      </Grid>
    </Container>
  );
} 